@import 'menu-slider';

.content-wrapper {
	height: 500px;
	width: 100%;
	padding-top: -5px;
	margin-top: -60px;
	background: url(img/content.png) top center repeat-x;
	z-index: 1;
	min-width: $min-width;
}

.content {
	/*position:absolute;*/
	/*margin-top: 3%;*/
	margin: auto;
	padding-top: 70px;
	/*width: 70em;*/
	width: $main-width;
	/*margin-left: 33%;*/
	/*padding-left: 10px;*/
	height: inherit;
	font-size: 0.9em;
	/*padding-right: 50px;*/
	background: url(img/content-img.png) center right no-repeat;
}

.content__item {
	float: right;
}

.content__header {
	margin-top: 10px;
	width: 440px;
	display: inline-block;
	line-height: inherit;
	background: url(img/about-h2-image.png) center right no-repeat;
	font-size: 0.8em;
}

.content-button {
	margin-left: 270px;
	display: inline-block;
	background: none;
	border: 1px solid $color-gray;
	@include border-radius(6px);
	height: 30px;
	font-size:0.9em;
	font-weight: bold;
	line-height: 30px;
	color: $color-gray;
}

.main__content {
	color: white;
	width: 846px;
	height: 314px;
	font-size: 1em;
	display: block;
	margin-right: 30px;
	background: url(img/main-content.png) center right no-repeat;
}

.content__text {
	position: relative;
	background: url(img/content-text.png) center left no-repeat;
	height: 200px;
	color: $color-yell;
	padding-top: 80px;
	font-size: 0.9em;	
}

.content__text h2 {
	padding-left: 20px;
}

.content__text p {
	padding-left: 20px;
	color: white;
	font-size: 1.4em;
}
