.service-wrapper {
	height: 400px;
	margin: auto;
	background: url(img/content.png) top center repeat-x;
	min-width: $min-width;
}

.service {
	width: $main-width;
	margin: auto;
}

.service__sentence {
	width: 70%;
	height: 400px;
	display: inline-block;
	vertical-align: top;
}

.service__delivery {
	width: 29%;
	height: 400px;
	display: inline-block;
	vertical-align: top;
}

.service__header-sentence {
	margin-top: 10px;
	width: 240px;
	display: inline-block;
	line-height: inherit;
	background: url(img/about-h2-image.png) center right no-repeat;
	font-size: 0.8em;
}

.service__header-delivery {
	margin-top: 10px;
	width: 280px;
	display: inline-block;
	line-height: inherit;
	background: url(img/about-h2-image.png) center right no-repeat;
	font-size: 0.8em;
}

.service__content {
	background: url(img/service-content.png) bottom left no-repeat;
	height: 300px;
}
.service__content-delivery {
	height: 300px;
}


.service__content-delivery ol {
	padding-left: 0px;
}

.service__content-delivery ol li{
	list-style: none;
	padding-top: 10px;
}

.service__num {
	color: white;
	background: black;
	display: inline-block;
	text-align: center;
	margin: 5px 10px;
	line-height: 25px;
	width: 25px;
	height: 25px;
	@include border-radius(60px);
}

.service__hrtop{
	margin-top: -8px;
	height: 8px;
	background: url(img/wacker-hr.png) top center repeat-x;	
}

